// eslint-disable-next-line no-unused-vars
export const orderDeleteReducer = (state = {}, action) => {
  switch (action.type) {
    case ORDER_DELETE_REQUEST:
      return {
        loading: true,
      }
    case ORDER_DELETE_SUCCESS:
      return {
        loading: false,
        success: true,
      }
    case ORDER_DELETE_FAIL:
      return {
        loading: false,
        error: action.payload,
      }
    default:
      return state
  }
}
